.i{
    display: flex;
    height: 100vh;
}

.i-left{
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
}

.i-right{
    flex: 1;
}

.i-left-wrapper {
    padding: 60px;
    height: 60%;
    display: flex;
    flex-direction: column;
}

.i-intro{
    font-size: 30px;
    font-weight: 300;
}

.i-name{
    font-size: 60px;
}

.i-title-wrapper {
 height: 100;
 animation: move 10s ease-in-out infinite alternate;
}

@keyframes move{
    25%{
        transform: translateY(-50px);
    }
    50%{
        transform: translateY(-100px);
    }
    75%{
        transform: translateY(-125px);
    }
    100%{
        transform: translateY(-150px);
    }
}

.i-title{
    height: 50px;
    overflow: hidden;
}

.i-title-item {
    height: 52;
    font-size: 30px;
    font-weight: bold;
    color: slategrey;
    display: flex;
    align-items: center;
    
}

.i-img{
    width: 100%;
    height: 150vh;
    object-fit: cover;
    top: 0;
    right: 0;
}

.bgcolor {
    width: 100%;
    height: 10%;
    background-image: url(../../images/ned.jpg);
    background-repeat: repeat;/* Determine what direction the image will repeat */
}

.nav-item .active{
   background-color: gray;  
}